section {
  min-width: 1400px;
  height: 100vh;
  background-color: hsl(235deg, 21%, 11%);
}
section .dark-image {
  background-image: url(../images/bg-desktop-dark.jpg);
  background-repeat: no-repeat;
  height: 30vh;
  background-size: 100vw;
}
section .card {
  width: 24.5rem;
  color: hsl(234deg, 11%, 52%);
  position: fixed;
  transform: translate(-50%, -13.5rem);
  top: 16.7rem;
  left: 50%;
  display: flex;
  flex-direction: column;
  gap: 1.3rem;
  overflow: visible;
}
section .card .toggle {
  color: hsl(236deg, 33%, 92%);
  display: grid;
  grid-auto-flow: column;
}
section .card .toggle h1 {
  letter-spacing: 0.5rem;
}
section .card .toggle .img {
  width: 1rem;
  background-image: url(../images/icon-sun.svg);
  background-repeat: no-repeat;
  height: 1.1rem;
  background-size: 1.1rem;
  align-self: baseline;
  justify-self: end;
}
section .card .add-todo {
  position: relative;
  top: 0.2rem;
  width: 93%;
  height: 2rem;
  background-color: hsl(235deg, 24%, 19%);
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.5rem 0.8rem;
}
section .card .add-todo input[type=text] {
  width: 80%;
  height: 2rem;
  background-color: hsl(235deg, 24%, 19%);
  border: none;
  color: hsl(234deg, 39%, 85%);
  caret-color: hsl(220deg, 98%, 61%);
}
section .card .add-todo input[type=text]::placeholder {
  color: hsl(234deg, 11%, 52%);
}
section .card .add-todo input[type=text]:focus {
  outline: none;
}
section .card .add-todo input[type=checkbox] {
  appearance: none;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  border: 1px solid hsl(233deg, 14%, 35%);
}
section .card .todo-list {
  width: 100%;
  background-color: hsl(235deg, 24%, 19%);
  box-shadow: 0 0 0 hsl(235deg, 21%, 11%), 0 3px 20px black;
}
section .card .todo-list .list {
  overflow: visible;
}
section .card .todo-list .list div {
  padding: 0.8rem;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: min-content;
  gap: 1rem;
}
section .card .todo-list .list div input[type=checkbox] {
  appearance: none;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  border: 1px solid hsl(233deg, 14%, 35%);
}
section .card .todo-list .list div input[type=checkbox]:checked:checked {
  appearance: none;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  background-image: linear-gradient(to right, hsl(192deg, 100%, 67%), hsl(280deg, 87%, 65%));
  background-repeat: no-repeat;
}
section .card .todo-list .list div input[type=checkbox]:checked:checked::after {
  content: "";
  width: 1.2rem;
  height: 1.2rem;
  position: absolute;
  background-image: url(../images/icon-check.svg);
  background-repeat: no-repeat;
  background-size: 0.6rem;
  background-position: 3.3px 3.3px;
}
section .card .todo-list .list div:hover input[type=checkbox] {
  border: double 1px transparent;
  background-image: linear-gradient(hsl(235deg, 24%, 19%), hsl(235deg, 24%, 19%)), linear-gradient(to right, hsl(192deg, 100%, 67%), hsl(280deg, 87%, 65%));
  background-origin: border-box;
  background-clip: content-box, border-box;
}
section .card .todo-list .list div li {
  list-style: none;
  color: hsl(234deg, 39%, 85%);
}
section .card .todo-list .list div:hover .delete {
  opacity: 1;
}
section .card .todo-list .list div .delete {
  opacity: 0;
  justify-self: end;
  align-self: center;
  width: 0.8rem;
  height: 0.8rem;
}
section .card .todo-list .list hr {
  border: none;
  width: 100%;
  height: 0.1px;
  background-color: hsl(237deg, 14%, 26%);
}
section .card .todo-list .list .li-checked {
  text-decoration-line: line-through;
  color: hsl(234deg, 11%, 52%);
}
section .card .todo-list .list .li-checked li {
  color: hsl(233deg, 14%, 35%);
}
section .card .todo-list .list .hide {
  display: none;
}
section .card .todo-list .filter-todos {
  display: grid;
  grid-auto-flow: column;
  gap: 2rem;
  justify-content: space-between;
  padding: 0.8rem 1rem;
  font-size: 0.8rem;
  cursor: pointer;
}
section .card .todo-list .filter-todos .filters {
  list-style: none;
  justify-self: center;
  display: grid;
  grid-auto-flow: column;
  gap: 0.6rem;
}
section .card .todo-list .filter-todos .filters .active {
  color: hsl(220deg, 98%, 61%);
}
section .card .todo-list .filter-todos .filters li:hover {
  color: hsl(236deg, 33%, 92%);
}
section .card .todo-list .filter-todos .delete-todos:hover {
  color: hsl(236deg, 33%, 92%);
}

