p {
  font-size: 18px;
  font-weight: 400;
}

h1 {
  font-size: 20px;
  font-weight: 700;
}

.list {
  font-weight: 500;
}

.filter-todos {
  font-size: 1rem;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

html {
  font-size: 75.5%;
  color: hsl(235deg, 21%, 11%);
  font-family: "https://fonts.google.com/specimen/Josefin+Sans";
}

::selection {
  color: hsl(220deg, 98%, 61%);
}